<template>
  <div class="mobile">
    <Navbar :pagesNavbar="this.pages" :topPages="this.topMenus" class="d-flex flex-column bd-highlight"/>
    <keep-alive>
      <Map v-if="isMobile" :lang="this.lang"/>
      <Default v-else class="vh-100" v-for="page in this.pages" :key="page.section" :section="page.section" :lang="this.lang" :images="page.images ?? []"/>
    </keep-alive>
  </div>
</template>
<script>

import {defineAsyncComponent} from "vue";

const Default = defineAsyncComponent(() => import( "@/components/Mobile/Layout/Default"));
const Navbar = defineAsyncComponent(() => import('@/components/Mobile/Navbar/Navbar'));
const Map = defineAsyncComponent(() => import('@/components/Mobile/Map/Map'));
import {Pages} from "@/assets/js/Mobile/PagesMobile";
import { HEADER_MENU } from '@/assets/js/Consts/HeaderMenu';

export default {
  components: {Default, Navbar,Map},
  props: ['lang'],
  data() {
    return {
      pages: Pages[this.lang],
      topMenus:HEADER_MENU[this.lang].topPages,
      isMobile:false
    };
  },
  watch: {
    '$store.state.isMobileMap': function (isMobile) {
      this.isMobile = isMobile
    },
  }
}
</script>
<style>
.mobile .modal-close {
  font-size: 3rem;
}
</style>